.main {
  display: flex;
  flex: 1 1 auto;
  flex-wrap: wrap;
  justify-content: space-between;

  &__nav {
    padding: 20px 0 20px 36px;
    width: 100%;
    max-width: 244px;
    background: $god-gray;
  }

  &__center-block {
    flex-grow: 3;
    padding: 20px 40px 20px 111px;
    width: auto;
  }

  &__sidebar {
    padding: 20px 90px 20px 78px;
    width: 100%;
    max-width: 418px;
  }
}
